.contact-us {
  background-color: #fff;
  padding: 120px 166px 65px 172px;
  display: flex;
  position: relative;
  font-size: 24px;
  justify-content: space-between;

  &__contact {
    width: 30%;
  }

  &__phone,
  &__adress {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 30px;

  }

  &__form {
    display: flex;
    flex-direction: column;
    width: 41%;
  }

  &__message {
    margin: 4px 0;
    border-width: 0;
    box-sizing: border-box;
    border-radius: 0;
    border-width: 0;
    padding: 11px 0 7px;
    background-color: #EEE;
    text-transform: uppercase;
    text-align: center;
    height: 166px;
    font-weight: 500;
    font-size: 14px;
    line-height: 36px;
    color: #393939;
    resize: none;
  }

  &__message::placeholder {
    color: #393939
  }







}
