body, p, li, ul, h1, h2{
  margin: 0;
  padding: 0;
  font-family: Ubuntu, sans-serif;
  font-size: 16px;
}

a, a:visited{
  text-decoration: none;
  text-transform: uppercase;
  box-sizing: border-box;
  color: #00AAEF;
}

a:hover {
  color: #2F80ED;
}

input {
  box-sizing: border-box;
  border-radius: 0;
  border-width: 0;
  padding: 11px 0 7px;
  background-color: #EEE;
  text-transform: uppercase;
  text-align: center;
  height: 54px;
  margin: 4px 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 36px;
  color: #393939;
}

input::placeholder {
  color: #393939;
  font-weight: 500;
}







